export function renderCard(data) {
    const src = data.webformatURL;
    const alt = data.tags;

//     ,    
// .   ',   
//    :

// webformatURL -       .
// largeImageURL -    .
// tags -    .    alt.
// likes -  .
// views -  .
// comments -  .
// downloads -  .
    return `
    <div class="photo-card">
  <img src="${src}" alt="${alt}" loading="lazy" />
  <div class="info">
    <p class="info-item">
      <b>Likes</b>
    </p>
    <p class="info-item">
      <b>Views</b>
    </p>
    <p class="info-item">
      <b>Comments</b>
    </p>
    <p class="info-item">
      <b>Downloads</b>
    </p>
  </div>
</div>`
}